import React, { Component } from 'react';
import ItemList from './itemList';
import SsCodeBox from './ssCodeBox';
import $ from "jquery/dist/jquery.min";
import CodeBox from './CodeBox';

class SelectApp extends Component {

  constructor(proprs) {
    super(proprs);
    var n = 15;
    var r = 50;
    var speed = 500;
    var arr = this.randomArr(n, r);
    var oArr = [];
    for (var i=0; i<n; i++){
      oArr[i] = arr[i];
    }
    var preCodeArr = ['for i = 0 to i = : //a[i]',
                      '\xa0\xa0for j = i + 1 to j = : //',
                      '\xa0\xa0\xa0\xa0if a[j] < a[i]: a[j]',
                      '\xa0\xa0\xa0\xa0else: continue',
                      '\xa0\xa0swap: a[i]']
    this.state = {
      interval: speed,
      sorting: 0,
      arr: arr,
      // intArr : arr,
      n: n,
      r: r,
      timeID: 0,
      i: -1,
      j: -1,
      p: [],
      k: -1,
      resetArr: oArr,
      initMeansRandom: 'none',
      initMeansInput: 'none',
      speeds:['', '', ''],
      preCodeArr: preCodeArr,
      hlp: -1,
      id: 0
    }

  }
  randomArr(n, r) {
    var arr = [];
    for (var i = 0; i < n; i++) {
      var value = Math.floor(Math.random() * r) + 1;
      arr.push(value)
    }
    return arr;
  }

  init() {
    this.stop();
    var arr = [];
    for (var i=0; i<this.state.n; i++){
      arr[i] = this.state.resetArr[i];
    }
    this.setState({
      arr: arr,
      n: this.state.n,
      timeID: 0,
      i: -1,
      j: -1,
      p: [],
      k: -1,
      interval: this.state.interval,
      sorting: 0,
      mixIndex:0,
      minValue:arr[0],
      resetArr: this.state.resetArr,
      id: 0,
      hlp: -1
    });
    
  }

  changeInitMeansInput(){
    this.setState({
      initMeansInput: 'block',
      initMeansRandom: 'none'
    });
  }

  changeInitMeansRandom(){
    this.setState({
      initMeansInput: 'none',
      initMeansRandom: 'block'
    });
  }

  render() {
    return (
      <div  className="app">
        <div className="Visualizer">
          <div className={"tutorial"+this.state.sorting}><code>i = {this.state.k}, j = {this.state.j}, a[j] = {this.state.arr[this.state.j]}, a[{this.state.i}] = {this.state.arr[this.state.i]}</code></div>
          <div className="canvas">
            <ItemList arr={this.state.arr} max={this.state.r} i={this.state.i} j={this.state.j} p={this.state.p} sorting={this.state.sorting}></ItemList>
          </div>
          <div className="buttons">
              <form action="selectApp.js">
                  &nbsp;<button type="button" className="btn-light" onClick={() => this.start()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <button type="button" className="btn-light" onClick={() => this.init()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <button type="button" className="btn-light" onClick={() => this.stop2()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  &nbsp;<button type="button" className="btn-light" onClick={() => this.nextstep()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <button type="button" className="btn-light" onClick={() => this.laststep()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <br></br><br></br>
                  &nbsp;
                  <button type="button" className="btn-light" onClick={() => this.changeInitMeansRandom()}></button>&nbsp;&nbsp;&nbsp;&nbsp;
                  <button type="button" className="btn-light" onClick={() => this.changeInitMeansInput()}></button>&nbsp;&nbsp;&nbsp;&nbsp;
                  &nbsp;
                    <select onChange={(e)=>this.getValue(e)}>
                      {
                        this.state.speeds.map((ele,index)=>{
                          return(
                            <option key={index}>{ele}</option>
                          )
                        })
                      }
                    </select>
                  <br></br><br></br>
                  <div id="console1" style={{display: this.state.initMeansInput}}>
                  <input placeholder="2050" maxLength = "100" size="50" id="inputArray"/>
                  &nbsp;&nbsp;<button type="button" className="btn-light" onClick={() => this.getUserInput()}></button><br></br>
                  </div>
                  <div id="console2" style={{display: this.state.initMeansRandom}}>
                  <input placeholder="120" id="randomNum"/>
                  &nbsp;&nbsp;<button type="button" className="btn-light" onClick={() => this.count(parseInt($('input#randomNum').val()))}></button>
                  <br></br>
                  </div>
              </form>  
            </div>
        </div>
        <div className="CodeBox">
                <div className="CodeTitle"></div>
                <CodeBox preCodeArr={this.state.preCodeArr} hlp={this.state.hlp} sorting={this.state.sorting}></CodeBox>
                <div className="CodeTitle"></div>
                <SsCodeBox></SsCodeBox>
        </div>
      </div>
    );
  }

  getUserInput(){
    this.stop();
    var a=$('input#inputArray').val().split(" ");
    var len = a.length;
    if(len > 20) len = 20;
    var arr=[];
    for (var i=0; i<len; ++i){
      if(parseInt(a[i]) <= 50){
        arr[i] = parseInt(a[i]);
      }
      else{
        arr[i] = 50;
      }
    }
    var oArr = [];
    for (var j = 0; j<arr.length; j++){
      oArr[j] = arr[j];
    }
    this.setState({
      arr: arr,
      n: arr.length,
      timeID: 0,
      i: -1,
      j: -1,
      p: [],
      k: -1,
      interval: this.state.interval,
      sorting: 0,
      id: 0,
      resetArr: oArr,
      hlp: -1
    })
  }

  getValue=(event)=>{
    console.log(event.target.value);
    if (event.target.value === '') this.speed(500);
    else if (event.target.value === '') this.speed(100);
    else if (event.target.value === '') this.speed(1);
  }

  count(n){
    this.stop();
    if (n > 20) n = 20;
    var arr = this.randomArr(n, this.state.r);
    var oArr = [];
    for (var i=0; i<n; i++){
      oArr[i] = arr[i];
    }
    this.setState({
      arr: arr,
      n: n,
      timeID: 0,
      i: -1,
      j: -1,
      p: [],
      k: -1,
      interval: this.state.interval,
      sorting: 0,
      resetArr: oArr,
      hlp: -1,
      id: 0
    });
  }

  speed(speed) {
    if (this.state.sorting === 1) {
      this.stop();
      var timeID = setInterval(
        () => this.sort(),
        speed
      );
      this.setState({
        timeID: timeID,
        sorting: 1,
        interval: speed
      });
    }else{
      this.setState({
        interval: speed
      });
    }

  }

  sort() {
    var arr = [];
    for(var x = 0; x < this.state.n; x++){
      arr[x] = this.state.arr[x];
    }
    var arrStates = this.selectionSort(arr);
    if(this.state.id < arrStates.length){
      const { arr: state, index1, index2, index3, complete: p, highlight } = arrStates[this.state.id];
      this.setState({ 
          arr: state,
          i: index1,
          j: index2,
          p: p,
          k: index3,
          hlp: highlight,
          id: this.state.id + 1});}
    else{this.stop();}
    
  }

  revSort() {
    var arr = [];
    for(var x = 0; x < this.state.n; x++){
      arr[x] = this.state.arr[x];
    }
    var arrStates = this.selectionSort(arr);
    if(this.state.id >= 1){
      const { arr: state, index1, index2, index3, complete: p,highlight } = arrStates[this.state.id];
      this.setState({ 
          arr: state,
          i: index1,
          j: index2,
          p: p,
          k: index3,
          hlp: highlight,
          id: this.state.id - 1});}
    else{this.stop();}
    
  }

  selectionSort(arr) {

    let arrStates = [];
    var p = [];
    for (var i = 0; i < arr.length - 1; i++) {
        var minId = i;
        const temp0 = { arr: arr.slice(), index1: minId, index3: i, complete: p.slice(), highlight: 0};
        arrStates.push(temp0);
        for (let j = i + 1; j < arr.length; j++) {
            const temp1 = { arr: arr.slice(), index1: minId, index2: j, index3: i, complete: p.slice(), highlight: 1};
            arrStates.push(temp1);
            if (arr[j] < arr[minId]) {
                minId = j;
                const temp2 = { arr: arr.slice(), index1: minId, index2: j, index3: i, complete: p.slice(), highlight: 2};
                arrStates.push(temp2);
            }
            else{
              const temp3 = { arr: arr.slice(), index1: minId, index2: j, index3: i, complete: p.slice(), highlight: 3};
              arrStates.push(temp3);
            }
            
        }
        const temp1 = { arr: arr.slice(), index1: minId, index2: i, index3: i, complete: p.slice(), highlight: 4};
        arrStates.push(temp1);
        const tempVal = arr[i];
        arr[i] = arr[minId];
        arr[minId] = tempVal;
        const temp2 = { arr: arr.slice(), index1: minId, index2: i, index3: i, complete: p.slice(), highlight: 4};
        arrStates.push(temp2);
        p.push(i);
        const temp = { arr: arr.slice(), index1: minId, index2: i, index3: i, complete: p.slice(), highlight: 4};
        arrStates.push(temp);
    }
    return arrStates;
  }

  start() {
    var timeID = this.state.timeID;
    var sorting = this.state.sorting;
    if (timeID === 0 && (sorting === 0||sorting ===2)) {
      timeID = setInterval(
        () => this.sort(),
        this.state.interval
      );
      this.setState({
        timeID: timeID,
        sorting: 1
      });
    }

  }

  nextstep(){
    this.stop();
    this.sort();
    this.setState({
      sorting: 2
    });
  }

  laststep(){
    this.stop();
    this.revSort();
    this.setState({
      sorting: 2
    });
  }
  
  stop() {
    var timeID = this.state.timeID;
    var sorting = this.state.sorting;
    if (timeID !== 0 && sorting === 1) {
      clearInterval(timeID);
      this.setState({
        timeID: 0,
        sorting: 0,
        hlp: -1
      });
    }

  }
  stop2() {
    var timeID = this.state.timeID;
    var sorting = this.state.sorting;
    if (timeID !== 0 && sorting === 1) {
      clearInterval(timeID);
      this.setState({
        timeID: 0,
        sorting: 2,
      });
    }

  }
}

export default SelectApp;
