import React, { Component } from 'react';
import '../aCss/app.css';
import ItemList from './itemList';
import QsCodeBox from './qsCodeBox'
import $ from "jquery/dist/jquery.min";
import CodeBox from './CodeBox';


class QuickApp extends Component {
  constructor(props) {
    super(props);
    var n = 15;
    var r = 50;
    var speed = 800;
    var arr = this.randomArr(n, r);
    var oArr = arr;
    for (var i=0; i<n; i++){
      oArr[i] = arr[i];
    }
    var preCodeArr = ['Partition(A, start, end) //startend',
                      '\xa0\xa0pivot = A[end]   //',
                      '\xa0\xa0i = start - 1 // i',
                      '\xa0\xa0for  j = start  to  end-1',
                      '\xa0\xa0//,',
                      '\xa0\xa0\xa0\xa0if  A[j] < pivot',
                      '\xa0\xa0\xa0\xa0\xa0\xa0i = i + 1',
                      '\xa0\xa0\xa0\xa0\xa0\xa0swap:A[i]A[j]',
                      '\xa0\xa0\xa0\xa0//,j',
                      '\xa0\xa0\xa0\xa0else continue',
                      '\xa0\xa0swap:A[i + 1]A[end]//',
                      '\xa0\xa0return i + 1//',
                      'end',
                      'QuickSort(A,start,end)',
                      '\xa0\xa0if start < end',
                      '\xa0\xa0\xa0\xa0pivot = Partition(A, start, end)//',
                      '\xa0\xa0\xa0\xa0QuickSort(A, start, pivot - 1)//',
                      '\xa0\xa0\xa0\xa0QuickSort(A, pivot + 1, end)//',
                      'end'];
    this.state = {
      arr: arr,
      n: n,
      r: r,
      timeID: 0,
      i: 0,
      j: 0,
      p: arr.length - 1,
      interval: speed,
      sorting: 0,
      resetArr: oArr,
      id: 0,
      initMeansRandom: 'none',
      initMeansInput: 'none',
      speeds:['', '', ''],
      preCodeArr:preCodeArr,
      hlp: -1
    }

  }

  randomArr(n, r) {
    var arr = [];
    for (var i = 0; i < n; i++) {
      var value = Math.floor(Math.random() * r) + 1;
      arr.push(value)
    }
    return arr;
  }

  init() {
    this.stop();
    var arr = [];
    for (var i=0; i<this.state.n; i++){
      arr[i] = this.state.resetArr[i];
    }
    this.setState({
      arr: arr,
      n: this.state.n,
      timeID: 0,
      i: 0,
      j: 0,
      p: arr.length - 1,
      interval: this.state.interval,
      resetArr: this.state.resetArr,
      sorting: 0,
      id: 0,
      hlp: -1
    });
  }

  changeInitMeansInput(){
    this.setState({
      initMeansInput: 'block',
      initMeansRandom: 'none'
    });
  }

  changeInitMeansRandom(){
    this.setState({
      initMeansInput: 'none',
      initMeansRandom: 'block'
    });
  }

  render() {
    return (
      <div className = "app">
          <div className="Visualizer">
            <div className={"tutorial"+this.state.sorting}><code>i = {this.state.i}, j = {this.state.j}, a[i] = {this.state.arr[this.state.i]}, a[j] = {this.state.arr[this.state.j]}, a[{this.state.p}] = {this.state.arr[this.state.p]}</code></div>
            <div className="canvas">
              <ItemList arr={this.state.arr} max={this.state.r} i={this.state.i} j={this.state.j} p={this.state.p} sorting={this.state.sorting}></ItemList>
            </div>
            <div className="buttons">
              <form action="quickApp.js">
                  &nbsp;<button type="button" className="btn-light" onClick={() => this.start()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <button type="button" className="btn-light" onClick={() => this.init()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <button type="button" className="btn-light" onClick={() => this.stop2()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  &nbsp;<button type="button" className="btn-light" onClick={() => this.nextstep()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <button type="button" className="btn-light" onClick={() => this.laststep()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <br></br><br></br>
                  &nbsp;
                  <button type="button" className="btn-light" onClick={() => this.changeInitMeansRandom()}></button>&nbsp;&nbsp;&nbsp;&nbsp;
                  <button type="button" className="btn-light" onClick={() => this.changeInitMeansInput()}></button>&nbsp;&nbsp;&nbsp;&nbsp;
                  &nbsp;
                    <select onChange={(e)=>this.getValue(e)}>
                      {
                        this.state.speeds.map((ele,index)=>{
                          return(
                            <option key={index}>{ele}</option>
                          )
                        })
                      }
                    </select>
                  <br></br><br></br>
                  <div id="console1" style={{display: this.state.initMeansInput}}>
                  <input placeholder="2050" maxLength = "100" size="50" id="inputArray"/>
                  &nbsp;&nbsp;<button type="button" className="btn-light" onClick={() => this.getUserInput()}></button><br></br>
                  </div>
                  <div id="console2" style={{display: this.state.initMeansRandom}}>
                  <input placeholder="120" id="randomNum"/>
                  &nbsp;&nbsp;<button type="button" className="btn-light" onClick={() => this.count(parseInt($('input#randomNum').val()))}></button>
                  <br></br>
                  </div>
              </form>  
            </div>
          </div>
          <div className="CodeBox">
                <div className="CodeTitle"></div>
                <CodeBox preCodeArr={this.state.preCodeArr} hlp={this.state.hlp} sorting={this.state.sorting}></CodeBox>
                <div className="CodeTitle"></div>
                <QsCodeBox></QsCodeBox>
          </div>
      </div>
      
    );
  }

  getUserInput(){
    this.stop();
    var a=$('input#inputArray').val().split(" ");
    var len = a.length;
    if(len > 20) len = 20;
    var arr=[];
    for (var i=0; i<len; ++i){
      if(parseInt(a[i]) <= 50){
        arr[i] = parseInt(a[i]);
      }
      else{
        arr[i] = 50;
      }
    }
    var oArr = [];
    for (var j = 0; j<arr.length; j++){
      oArr[j] = arr[j];
    }
    this.setState({
      arr: arr,
      n: arr.length,
      timeID: 0,
      i: 0,
      j: 0,
      p: arr.length - 1,
      interval: this.state.interval,
      sorting: 0,
      id: 0,
      resetArr: oArr,
      hlp: -1
    })
  }

  getValue=(event)=>{
    console.log(event.target.value);
    if (event.target.value === '') this.speed(800);
    else if (event.target.value === '') this.speed(200);
    else if (event.target.value === '') this.speed(1);
  }

  count(n){
    this.stop();
    if (n > 20) n = 20;
    var arr = this.randomArr(n, this.state.r);
    var oArr = [];
    for (var i=0; i<n; i++){
      oArr[i] = arr[i];
    }
    this.setState({
      arr: arr,
      n: n,
      timeID: 0,
      i: 0,
      j: 0,
      p: this.state.arr.length - 1,
      interval: this.state.interval,
      sorting: 0,
      id: 0,
      resetArr: oArr,
      hlp: -1
    });
  }

  speed(speed) {
    if (this.state.sorting === 1) {
      this.stop();
      var timeID = setInterval(
        () => this.sort(),
        speed
      );
      this.setState({
        timeID: timeID,
        sorting: 1,
        interval: speed
      });
    }else{
      this.setState({
        interval: speed
      });
    }

  }

  sort() {
    var arr = [];
    for(var x = 0; x < this.state.n; x++){
      arr[x] = this.state.arr[x];
    }
    var arrStates = this.quickSortStates(arr);
    if(this.state.id < arrStates.length){
      const { arr: state, index1, index2, pivot, highlight } = arrStates[this.state.id];
      this.setState({ 
          arr: state,
          i: index1,
          j: index2,
          p: pivot, 
          hlp: highlight,
          id: this.state.id + 1});}
    else{this.stop();}
    
  }

  revSort() {
    var arr = [];
    for(var x = 0; x < this.state.n; x++){
      arr[x] = this.state.arr[x];
    }
    var arrStates = this.quickSortStates(arr);
    if(this.state.id >= 1){
      const { arr: state, index1, index2, pivot, highlight } = arrStates[this.state.id];
      this.setState({ 
          arr: state,
          i: index1,
          j: index2,
          p: pivot, 
          hlp: highlight,
          id: this.state.id - 1});}
    else{this.stop();}
  }
  
  quickSortStates(arr) {
    var arrStates = [];
    this.qsHelper(arr, 0, arr.length - 1, arrStates);
    return arrStates;
  }
  
  qsHelper(arr, start, end, arrStates){
    if (start < end) {
      var pivot = this.partition(arr, start, end, arrStates);
      this.qsHelper(arr, start, pivot - 1, arrStates);
      this.qsHelper(arr, pivot + 1, end, arrStates);
  }
  }

  partition(arr, start, end, arrStates) {
    var pivot = arr[end];
    const temp0 = { arr: arr.slice(), pivot: end, highlight: 0};
    arrStates.push(temp0);

    var i = start - 1;
    const temp2 = { arr: arr.slice(), index1: i, pivot: end, highlight: 2};
    arrStates.push(temp2);
    for (let j = start; j < end; j++) {
        const temp3 = { arr: arr.slice(), index1: i, index2: j, pivot: end, highlight: 3};
        arrStates.push(temp3);
        if (arr[j] < pivot) {
           const temp5 = { arr: arr.slice(), index1: i, index2: j, pivot: end, highlight: 5};
           arrStates.push(temp5);

            i++;
            const temp6 = { arr: arr.slice(), index1: i, index2: j, pivot: end, highlight: 6};
            arrStates.push(temp6);

            const temp1 = { arr: arr.slice(), index1: i, index2: j, pivot: end, highlight: 7};
            arrStates.push(temp1);
            const tempVal = arr[i];
            arr[i] = arr[j];
            arr[j] = tempVal;
            const temp2 = { arr: arr.slice(), index1: i, index2: j, pivot: end, highlight: 7};
            arrStates.push(temp2);
        }
        else{
          const temp = { arr: arr.slice(), index1: i, index2: j, pivot: end, highlight: 9};
          arrStates.push(temp);
        }
    }
    const temp3 = { arr: arr.slice(), index1: i+1, index2: end, pivot: end, highlight: 10};
    arrStates.push(temp3);
    const tempVal = arr[i+1];
    arr[i+1] = arr[end];
    arr[end] = tempVal;
    const temp4 = { arr: arr.slice(), index1: i+1, index2: end, pivot: end, highlight: 10};
    arrStates.push(temp4);
    return i + 1;
 }

  start() {
    var timeID = this.state.timeID;
    var sorting = this.state.sorting;
    if (timeID === 0 && (sorting === 0||sorting === 2)) {
      timeID = setInterval(
        () => this.sort(),
        this.state.interval
      );
      this.setState({
        timeID: timeID,
        sorting: 1
      });
    }

  }

  nextstep(){
    this.stop();
    this.sort();
    this.setState({
      sorting: 2
    });
  }

  laststep(){
    this.stop();
    this.revSort();
    this.setState({
      sorting: 2
    });
  }
  
  stop() {
    var timeID = this.state.timeID;
    var sorting = this.state.sorting;
    if (timeID !== 0 && sorting === 1) {
      clearInterval(timeID);
      this.setState({
        timeID: 0,
        sorting: 0,
        hlp: -1
      });
    }

  }
  stop2() {
    var timeID = this.state.timeID;
    var sorting = this.state.sorting;
    if (timeID !== 0 && sorting === 1) {
      clearInterval(timeID);
      this.setState({
        timeID: 0,
        sorting: 2,
      });
    }
  }

}

export default QuickApp;
