import React, { Component } from 'react';
import '../aCss/app.css';
import ItemList from './itemList';
import MsCodeBox from './msCodeBox'
import $ from "jquery/dist/jquery.min";
import CodeBox from './CodeBox';

class MergeApp extends Component {
  constructor(props) {
    super(props);
    var n = 16;
    var r = 50;
    var speed = 800;
    var arr = this.randomArr(n, r);
    var oArr = [];
    for (var i=0; i<n; i++){
      oArr[i] = arr[i];
    }
    var preCodeArr = ['merge(A,start,mid,end) //mid,end',
                      '\xa0\xa0AC = copy(A) //AAC',
                      '\xa0\xa0i = start; //,',
                      '\xa0\xa0j = mid + 1; //,',
                      '\xa0\xa0k = start; //,',
                      '\xa0\xa0while(i <= mid and j <= end)',
                      '\xa0\xa0//',
                      '\xa0\xa0\xa0\xa0if(AC[i] <= AC[j]): A[k++] = AC[i++];',
                      '\xa0\xa0\xa0\xa0else:A[k++] = AC[j++];',
                      '\xa0\xa0\xa0\xa0//',
                      '\xa0\xa0while(i <= mid): A[k++] =AC[i++];',
                      '\xa0\xa0//',
                      '\xa0\xa0while(j <= end): A[k++] =AC[j++];',
                      '\xa0\xa0//',
                      'mergeSort(A,start,end)',
                      '\xa0\xa0if (start == end) return //',
                      '\xa0\xa0mid = (start + end)/2; //mid',
                      '\xa0\xa0mergeSort(A, start, mid); //',
                      '\xa0\xa0mergeSort(A, mid+1, end); //',
                      '\xa0\xa0merge(A, start, mid, end); //',
                      'end'];
    this.state = {
      arr: arr,
      arrC: [],
      n: n,
      r: r,
      timeID: 0,
      index: [],
      interval: speed,
      sorting: 0,
      resetArr: oArr,
      id: 0,
      initMeansRandom: 'none',
      initMeansInput: 'none',
      speeds:['', '', ''],
      preCodeArr: preCodeArr,
      hlp: -1
    }

  }

  randomArr(n, r) {
    var arr = [];
    for (var i = 0; i < n; i++) {
      var value = Math.floor(Math.random() * r) + 1;
      arr.push(value)
    }
    return arr;
  }

  random(){
    this.stop();
    var arr = this.randomArr(this.state.n, this.state.r);
    var oArr = [];
    for (var i = 0; i < this.state.n; i++){
      oArr[i] = arr[i];
    }
    this.setState({
      arr: arr,
      arrC: [],
      n: this.state.n,
      timeID: 0,
      index: [],
      interval: this.state.interval,
      sorting: 0,
      resetArr: oArr,
      id: 0,
      hlp: -1
    });
  }

  init() {
    this.stop();
    var arr = [];
    for (var i=0; i<this.state.n; i++){
      arr[i] = this.state.resetArr[i];
    }
    this.setState({
      arr: arr,
      arrC: [],
      n: this.state.n,
      timeID: 0,
      index: [],
      interval: this.state.interval,
      resetArr: this.state.resetArr,
      sorting: 0,
      id: 0,
      hlp: -1
    });
  }

  changeInitMeansInput(){
    this.setState({
      initMeansInput: 'block',
      initMeansRandom: 'none'
    });
  }

  changeInitMeansRandom(){
    this.setState({
      initMeansInput: 'none',
      initMeansRandom: 'block'
    });
  }


  render() {
    return (
      <div className = "app">
          <div className="Visualizer0">
            <div className={"tutorial"+this.state.sorting}>
              <code>i = {this.state.index[0]}, j = {this.state.index[1]}, AC[i] = {this.state.arr[this.state.index[0]]}, 
              AC[j] = {this.state.arr[this.state.index[1]]}, k = {this.state.index[2]}, A[k] = {this.state.arr[this.state.index[2]]}</code>
            </div>
            <div className="Visualizer1">
              {/* <div className="canvas"> */}
                <ItemList arr={this.state.arr} max={this.state.r} index={this.state.index} sorting={this.state.sorting} id={0}></ItemList>
              {/* </div> */}
              {/* <div className="canvas"> */}
                <ItemList arr={this.state.arrC} max={this.state.r} index={this.state.index} sorting={this.state.sorting} id={1}></ItemList>
              {/* </div>  */}
            </div>
            <div className="buttons">
              <form action="mergeApp.js">
                  &nbsp;<button type="button" className="btn-light" onClick={() => this.start()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <button type="button" className="btn-light" onClick={() => this.init()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <button type="button" className="btn-light" onClick={() => this.stop2()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  &nbsp;<button type="button" className="btn-light" onClick={() => this.nextstep()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <button type="button" className="btn-light" onClick={() => this.laststep()}></button><span >&nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <br></br><br></br>
                  &nbsp;
                  <button type="button" className="btn-light" onClick={() => this.changeInitMeansRandom()}></button>&nbsp;&nbsp;&nbsp;&nbsp;
                  <button type="button" className="btn-light" onClick={() => this.changeInitMeansInput()}></button>&nbsp;&nbsp;&nbsp;&nbsp;
                  
                  &nbsp;
                    <select onChange={(e)=>this.getValue(e)}>
                      {
                        this.state.speeds.map((ele,index)=>{
                          return(
                            <option key={index}>{ele}</option>
                          )
                        })
                      }
                    </select>
                  <br></br><br></br>
                  <div id="console1" style={{display: this.state.initMeansInput}}>
                  <input placeholder="2050" maxLength = "100" size="50" id="inputArray"/>
                  &nbsp;&nbsp;<button type="button" className="btn-light" onClick={() => this.getUserInput()}></button><br></br>
                  </div>
                  <div id="console2" style={{display: this.state.initMeansRandom}}>
                  <input placeholder="120" id="randomNum"/>
                  &nbsp;&nbsp;<button type="button" className="btn-light" onClick={() => this.count(parseInt($('input#randomNum').val()))}></button>
                  <br></br>
                  </div>
              </form>  
            </div>
            
          </div>
          <div className="CodeBox0">
                <div className="CodeTitle"></div>
                <CodeBox preCodeArr={this.state.preCodeArr} hlp={this.state.hlp} sorting={this.state.sorting}></CodeBox>
                <div className="CodeTitle"></div>
                <MsCodeBox></MsCodeBox>
          </div>
      </div>
      
    );
  }

  getUserInput(){
    this.stop();
    var a=$('input#inputArray').val().split(" ");
    var len = a.length;
    if(len > 20) len = 20;
    var arr=[];
    for (var i=0; i<len; ++i){
      if(parseInt(a[i]) <= 50){
        arr[i] = parseInt(a[i]);
      }
      else{
        arr[i] = 50;
      }
    }
    var oArr = [];
    for (var j = 0; j<arr.length; j++){
      oArr[j] = arr[j];
    }
    this.setState({
      arr: arr,
      arrC: [],
      n: arr.length,
      timeID: 0,
      index: [],
      interval: this.state.interval,
      sorting: 0,
      id: 0,
      resetArr: oArr,
      hlp: -1
    })
  }

  getValue=(event)=>{
    console.log(event.target.value);
    if (event.target.value === '') this.speed(800);
    else if (event.target.value === '') this.speed(200);
    else if (event.target.value === '') this.speed(1);
  }

  count(n){
    this.stop();
    if (n > 20) n = 20;
    var arr = this.randomArr(n, this.state.r);
    var oArr = [];
    for (var i = 0; i < n; i++){
      oArr[i] = arr[i];
    }
    this.setState({
      arr: arr,
      arrC: [],
      n: n,
      timeID: 0,
      index: [],
      p: this.state.arr.length - 1,
      interval: this.state.interval,
      sorting: 0,
      id: 0,
      resetArr: oArr,
      hlp: -1
    });
  }

  speed(speed) {
    if (this.state.sorting === 1) {
      this.stop();
      var timeID = setInterval(
        () => this.sort(),
        speed
      );
      this.setState({
        timeID: timeID,
        sorting: 1,
        interval: speed
      });
    }else{
      this.setState({
        interval: speed
      });
    }

  }

  sort() {
    var arr = [];
    for(var x = 0; x < this.state.n; x++){
      arr[x] = this.state.resetArr[x];
    }
    var arrStates = this.mergeSort(arr);
    if(this.state.id < arrStates.length){
      const { arr: state, index: changing, arrC: stateC, highlight } = arrStates[this.state.id];
      this.setState({ 
          arr: state,
          arrC: stateC,
          index: changing, 
          hlp: highlight,
          id: this.state.id + 1
      } );
    } 
    else{this.stop();}
    
  }

  revSort() {
    var arr = [];
    for(var x = 0; x < this.state.n; x++){
      arr[x] = this.state.resetArr[x];
    }
    var arrStates = this.mergeSort(arr);
    if(this.state.id >= 1){
      const { arr: state, index: changing, arrC: stateC, highlight } = arrStates[this.state.id];
      this.setState({ 
          arr: state,
          arrC: stateC,
          index: changing, 
          hlp: highlight,
          id: this.state.id - 1
      } );
    } 
    else{this.stop();}
  }

  mergeSort(arr) {
    var arrStates = [];
    this.mergeSortHelper(arr, 0, arr.length - 1, arrStates);
    const temp = { arr: this.state.resetArr.slice(), index: [], arrC: arr.slice()};
    arrStates.push( temp );
    return arrStates;
  }

  mergeSortHelper(arr, start, end, arrStates) {
    if (start === end) return;
    const mid = Math.floor((start + end) / 2);
    this.mergeSortHelper(arr, start, mid, arrStates);
    this.mergeSortHelper(arr, mid + 1, end, arrStates);
    this.merge( arr, start, mid, end, arrStates );
  }

  merge(arr, start, mid, end, arrStates) {
  var k = start, i = start, j = mid + 1;
  var arrC = arr.slice();
  while ( i <= mid && j <= end )
  {
    const temp = { arr: arrC.slice(), index: [i, j, k, -1, 0, 0], arrC: arr.slice(0, k), highlight: 5};
    arrStates.push( temp );
    if ( arrC[i] <= arrC[j] )
    {
      arr[k] = arrC[i];
      const temp = { arr: arrC.slice(), index: [i, j, k, 0, 0, 1], arrC: arr.slice(0, k + 1), highlight: 7};
      arrStates.push( temp );
      k++;
      i++;
    }
    else
    {
      arr[k] = arrC[j];
      const temp = { arr: arrC.slice(), index: [i, j, k, 1, 1, 0], arrC: arr.slice(0, k + 1), highlight: 8};
      arrStates.push( temp );
      k++;
      j++;
    }
  }
  while ( i <= mid )
  {
    arr[k] = arrC[i];
    const temp = { arr: arrC.slice(), index: [i, j, k, 0, 0, 1], arrC: arr.slice(0, k + 1), highlight: 10};
    arrStates.push(temp);
    k++;
    i++;
  }
  while ( j <= end )
  {
    arr[k] = arrC[j];
    const temp = { arr: arrC.slice(), index: [i, j, k, 1, 1, 0], arrC: arr.slice(0, k + 1), highlight: 12};
    arrStates.push(temp);
    k++;
    j++;
  }
  }

  start() {
    this.stop();
    var timeID = this.state.timeID;
      var sorting = this.state.sorting;
    //   var arrStates = this.mergeSort(arr);
    if (timeID === 0 && (sorting === 0||sorting === 2)) {
      timeID = setInterval(
        () => this.sort(),
        this.state.interval
      );
      this.setState({
        timeID: timeID,
        sorting: 1
      });
    }

  }

  nextstep(){
    this.stop();
    this.sort();
    this.setState({
      sorting: 2
    });
  }

  laststep(){
    this.stop();
    this.revSort();
    this.setState({
      sorting: 2
    });
  }
  
  stop() {
    var timeID = this.state.timeID;
    var sorting = this.state.sorting;
    if (timeID !== 0 && sorting === 1) {
      clearInterval(timeID);
      this.setState({
        timeID: 0,
        sorting: 0,
        hlp: -1
      });
    }

  }
  stop2() {
    var timeID = this.state.timeID;
    var sorting = this.state.sorting;
    if (timeID !== 0 && sorting === 1) {
      clearInterval(timeID);
      this.setState({
        timeID: 0,
        sorting: 2,
      });
    }
  }

}

export default MergeApp;
